
const Component = require('../../../ui-base/component');
const template = require('./index.html');
const _ = require('../../../ui-base/_');

/**
 * @class KLMessage
 * @extend Component
 * @param {object}      [options.data]                      = 
 * @param {string}      [options.data.type]                 => `success``warning``info``error`
 * @param {string}      [options.data.class]                => `class`
 */

const KLMessage = Component.extend({
  name: 'kl-message',
  template,
  config() {
    _.extend(this.data, {
      type: '',
    });
    this.supr();
  },
  init() {
    this.supr();
  },
});
module.exports = KLMessage;



