/**
 * @file KLSidebar
 * @author   sensen(rainforest92@126.com)
 */

const Component = require('../../../ui-base/component');
const template = require('./index.html');

/**
 * @class KLSidebar
 * @extend Component
 * @param {object}        [options.data]                          => 
 * @param {string}        [options.data.class]                    => class
 * @param {array}         [options.data.menus]                    => 
 * @param {string}        [options.data.top=60px]               => style top
 * @param {boolean}       [options.data.active=true]              => 
 * @param {string}        [options.data.bodyEl]                => bodyid,,bodyEl
 * @param {boolean}       [options.data.uniqueOpened=true]        => 
 * @param {string}        [options.data.titleKey=title]           => key
 * @param {string}        [options.data.urlKey=url]             => key
 * @param {string}        [options.data.pageKey=title]          => key
 * @param {string}        [options.data.childrenKey=children]   => key
 */
const KLSidebar = Component.extend({
  name: 'kl-sidebar',
  template,
  config() {
    this.defaults({
      class: '',
      uniqueOpened: true,
      menus: [],
      titleKey: 'title',
      urlKey: 'url',
      pageKey: 'title',
      childrenKey: 'children',
      top: '60px',
      active: true,
      bodyEl: '',
    });

    this.supr();
  },
  initBodyEl() {
    if (this.data.bodyEl) {
      this.data.$bodyEl = document.getElementById(this.data.bodyEl);
      if (this.data.$bodyEl) {
        this.data.$bodyEl.style.transition = 'left .3s';
      }
    }
  },
  toggle() {
    this.initBodyEl();

    this.data.active = !this.data.active;
    if (this.data.$bodyEl) {
      this.data.$bodyEl.style.left = this.data.active ? '180px' : '0';
    }

    /**
     * @event KLSidebar#toggle 
     * @property {boolean} active 
     */
    this.$emit('toggle', this.data.active);
  },
  /**
   * @event KLSidebar#menuitem-click 
   * @property {object} menuitem menuItem
   */
  onMenuItemClick(e) {
    this.$emit('menuitem-click', e);
  },
});

KLSidebar.directive('top', function (ele, value) {
  this.$watch(value, (top) => {
    ele.style.top = top;
  });
});

module.exports = KLSidebar;



