/**
 * @file KLSteps     
 * @author   ziane(zianecui@gmail.com)
 */

const Component = require('../../../ui-base/component');
const template = require('./index.html');
const _ = require('../../../ui-base/_');

/**
 * @class KLSteps
 * @extend Component
 * @param {object}      [options.data]                        = 
 * @param {object[]}    [options.data.steps=null]             <=> 
 * @param {number}      [options.data.steps[].status]         => id
 * @param {string}      [options.data.steps[].title]          => 
 * @param {object[]}    [options.data.steps[].description]    => 
 * @param {number}      [options.data.current=0]              <=> 
 * @param {string}      [options.data.size]                   =>  sm
 */
const KLSteps = Component.extend({
  name: 'kl-steps',
  template,
  config() {
    _.extend(this.data, {
      steps: [],
      current: 0,
      size: '',
      currentIndex: 0,
    });
    this.supr();
  },
  init() {
    this.supr();
    this.$watch('current', () => {
      this.juedgeFinishedItem();
    });
  },
  juedgeFinishedItem() {
    const data = this.data;
    const current = data.current;
    const steps = data.steps;

    steps.forEach((item, index) => {
      if (item.status / 1 === current / 1) {
        data.currentIndex = index;
      }
    });
  },
});

module.exports = KLSteps;



