/**
 * @file KLButton 
 * @author Cody Chan <int64ago@gmail.com>
 */

const validator = require('validator');
const bowser = require('bowser');
const Component = require('../../../ui-base/component');
const template = require('./index.html');
const _ = require('../../../ui-base/_');

/**
 * @class KLButton
 * @param {object}      [options.data]                = 
 * @param {string}      [options.data.title=]      => 
 * @param {string}      [options.data.type=default]   => , primary, secondary, default
 * @param {string}      [options.data.size=normal]    => , sm
 * @param {string}      [options.data.icon]           => ,action;
 * @param {string}      [options.data.action]         => , icon;
 * @param {string}      [options.data.link]           => 
 * @param {string}      [options.data.target=_self]   => 
 * @param {string}      [options.data.download]       => 
 * @param {boolean}     [options.data.loading=false]  => 
 * @param {boolean}     [options.data.disabled=false] => 
 * @param {boolean}     [options.data.class=false]    => 
 */
const actionIcons = {
  /*  */
  view: 'eye',
  /*  */
  send: 'send-o',
  /*  */
  undo: 'reply-all',
  /*  */
  cancel: 'arrow-circle-left',
  /*  */
  remove: 'trash',
  /*  */
  update: 'refresh',
  /*  */
  submit: 'legal',
  /*  */
  save: 'save',
  /*  */
  copy: 'copy',
  /*  */
  pass: 'success',
  /*  */
  reject: 'warning',
  /*  */
  backward: 'angle-double-left',
  /*  */
  download: 'download',
  /*  */
  upload: 'upload',
  /*  */
  search: 'search',
  /*  */
  edit: 'edit',
  /*  */
  add: 'add',
  /*  */
  link: 'link',
  /*  */
  plus: 'plus',
};

const KLButton = Component.extend({
  name: 'kl-button',
  template,
  config() {
    _.extend(this.data, {
      title: this.$trans('CONFIRM'),
      type: 'default',
      size: 'normal',
      icon: '',
      loading: false,
      disabled: false,
      actionIcons,
      target: '_self',
    });
    this.supr();
  },

  init() {
    this.supr();
    this.$watch('download', function (url) {
      if (validator.isURL(url)) {
        if (bowser.chrome) {
          const a = document.createElement('a');
          a.href = url;
          a.download = url;
          a.click();
        } else {
          location.href = url;
        }
        this.data.download = '';
      }
    });
  },
  /**
   * @event KLButton#click 
   * @property {object} sender 
   * @property {object} e event
   */
  onClick(e) {
    const loading = this.data.loading;
    if (!loading) {
      this.$emit('click', {
        sender: this,
        e,
      });
    }
    return !!this.data.link;
  },

  onMouseUp(e) {
    e.target.blur();
  },
});

module.exports = KLButton;



